                h3.color-adicional-4 ¿Qué es una escala Likert?
                p.color-adicional-4 Es un instrumento de medición que permite captar actitudes y percepciones mediante una serie de afirmaciones que los participantes califican según su grado de acuerdo o desacuerdo.

              .col-lg-3.col-6
                figure
