                .color-adicional-4 Se presentan afirmaciones sobre la experiencia.
                .color-adicional-4 Los participantes eligen entre opciones predefinidas.
                .color-adicional-4 Generalmente usa 5 puntos, de 1 (Totalmente en desacuerdo) a 5 (Totalmente de acuerdo).
                .color-adicional-4 Proporciona datos cuantitativos para análisis e interpretación de la percepción de la experiencia del cliente.


              .col-lg-3.col-6
