                .color-adicional-4 Opciones:
                .color-adicional-4 1. Totalmente en desacuerdo
                .color-adicional-4 2. En desacuerdo
                .color-adicional-4 3. Neutral
                .color-adicional-4 4. De acuerdo
                .color-adicional-4 5. Totalmente de acuerdo

